.section-padding{
  padding: 4rem 4rem;
}
.footer{
  background-color: #2d2d32;
}
._footer{
  display: flex;
  flex-direction: column;
}
.footer-links{
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  text-align: left;
  margin-bottom: 2rem;
}
.links-div{
  width: 150px;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  color: white;
}
a{
  color: rgb(175, 175, 179);
  text-decoration: none;
}
.socialmedia{
  display: flex;
  flex-direction: row;
}
.socialmedia p{
  width: 80%;
}
.links-div h4{
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 0.9rem;
}
.links-div p{
  font-size: 12px;
  line-height: 15px;
  margin: 0.5rem 0;
  cursor: pointer;
}
.footer-below{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 0.2rem;
}
.footer-below-links{
  display: flex;
  flex-direction: row;
}
.footer-below-links p{
  font-size: 13px;
  line-height: 15px;
  margin-left: 2rem;
  color: white;
  font-weight: 600;
}
hr{
  color: white !important;
  width: 100%;

}
.footer-copyright p{
  font-size: 13;
  line-height: 15px;
  color: rgb(255,255,255);
  font-weight: 600;
}
@media screen and (max-width: 850px) {
  .footer-heading h1{
    font-size: 44px;
    line-height: 50px;
  }
  
}
@media screen and (max-width: 550px) {
  .footer-heading h1{
    font-size: 34px;
    line-height: 42px;
  }
  .footer-links div{
    margin: 1rem 0;
  }
  .footer-btn p{
    font-size: 14px;
    line-height: 20px;
    
  }
  .footer-below{
    flex-direction: column;
    justify-content: left;
  }
  .footer-below-links{
    flex-direction: column;
  }
  .footer-below-links p{
    margin-left: 0rem;
    margin-top: 1rem;
  }
  
}
@media screen and (max-width: 400px) {
  .footer-heading h1{
    font-size: 27px;
    line-height: 38px;

  }
  
}